.edit_container {
    .edit_form {
        .update_btn {
            border-style: solid;
            border-top-width: 2px;
            border-right-width: 2px;
            border-left-width: 2px;
            border-bottom-width: 2px;
            color: #54c7e1;
            border-color: #54c7e1;
            background-color: rgba(255,255,255,0);
